<!-- Lynn -->
<script>
import header1 from "@/components/Header1/header1.vue";
import config from "../../../../public/config/customSetting.json";

export default {
  name: "viewSignOffDetails-component",
  components: { header1 },
  data() {
    return {
      projDetail: [], // 工程基本資料
      work_Type: [], // 高風險作業列表
      attachment: [], // 工程附件檔案
      vendor_Detail: [], // 承包商資料
      head: [], // 待簽核人員、待簽核節點
      detail: [], // (已)簽核點
    };
  },
  computed: {
    // 用 {模板語法}，重製高風險工種列表
    workType() {
      return this.work_Type
        .reduce((acc, curr) => {
          acc.push(`${curr.work_Type_Name} (${curr.description})`);
          return acc;
        }, [])
        .join("、");
    },

    // 主承包商 M、下承包商 S (可能為無或多家)
    projectVendorDetailM() {
      return this.vendor_Detail.filter((x) => x.vendor_Type == "M");
    },
    projectVendorDetailS() {
      return this.vendor_Detail.filter((x) => x.vendor_Type == "S");
    },

    // 用 {模板語法}，重製待簽核人員列表及節點
    waitStaff() {
      return this.head
        .map((x) => `${x.name} (${x.processor_EmpNo}/${x.ext_No}/${x.e_mail})`)
        .join("、");
    },
    waitNode() {
      return this.head.map((x) => x.act_Desc).join("、");
    },
  },
  methods: {
    // 從對應 controller 下載文件，並指定檔名
    async downloadPdf(item) {
      try {
        const res = await this.$store.dispatch("commonDownloadPdf", {
          file_id: item.file_Id,
          router_Name: config.getProjMgmtController,
        });
        this.downloadFile(res, item.file_Name, "pdf");
      } catch (err) {
        console.log(err);
      }
    },
  },
  async mounted() {
    this.$root.$children[0].sidebarSwitch = false;
    this.$store.commit("HEADER_HANDLER", false);
    this.$store.commit("BREADCRUMB_HANDLER", false);

    // $store 更新 query 夾帶之資料
    let data = JSON.parse(this.$route.query.data);
    if (data) {
      this.$root.overlay = false;
    }
    this.projDetail = data[0].proj_Detail;
    this.work_Type = data[0].work_Type;
    this.attachment = data[0].attachment;
    this.vendor_Detail = data[0].vendor_Detail;
    this.head = data[1].head;
    this.detail = data[1].detail;
  },
};
</script>

<template src="./viewSignOffDetails.html"></template>
